    transition: .4s ease-in-out;
    transform-origin: left;
    transform: scaleX(0);
  }
  nav a:hover:after,nav a.active:after{
    transform: scaleX(1);
  }
  nav a:hover,nav a.active{
    color: #ffffff;
  }
  .downloadBtnContainer{
    display: flex;
    margin-left: auto;
  }
  .dwnlBtn{
    transition: .4s ease-in-out;
    height: 48px;
    display: flex;
    align-items: center;
    color: #fff;
    text-transform: uppercase;
    padding: 0px 30px;
    border-radius: 6px;
    position: relative;
    overflow: hidden;
  }
  .dwnlBtn span{
    position: relative;
    z-index: 3;
    color: #fff;
  }
  .dwnlBtn::before{
    content: '';
