    z-index: 0;
    background: rgb(57,126,244);
    background: linear-gradient(93deg, rgba(57,126,244,1) 0%, rgba(73,223,143,1) 100%);
    transition: .4s ease-in-out;
  }
  .dwnlBtn::after{
