    }
    nav{
      flex-direction: column;
      display: flex;
      transition: .4s ease-in-out;
      transform: translateY(10px);
      opacity: 0;
    }
    .activeBurger nav{
      transform: translateY(0px);
      opacity: 1;
    }
    .downloadBtnContainer{
      transition: .4s ease-in-out;
      transform: translateY(10px);
